import React from 'react'
import img_vs from '../assets/img/img_consult.png'
import '../css/Section1.css'
import AssignmentTurnedInOutlinedIcon from '@mui/icons-material/AssignmentTurnedInOutlined';
import ManageAccountsOutlinedIcon from '@mui/icons-material/ManageAccountsOutlined';
import GroupsOutlinedIcon from '@mui/icons-material/GroupsOutlined';

const Section1 = () => {
    return (
        <div id="section_1" className='main_section1'>
            <div className='txt_content'>
                <h2>Nuestros Servicios</h2>
                <p>A travs de las mejores prcticas de TI, damos soluciones de gran valor a nuestros clientes a travs de nuestros servicios.</p>
            </div>
            <div className='cards'>
                <div className='card_1'>
                    <img className='img_box' src={img_vs} alt="captura visual code" />
                    <div className='content_card_1'>
                        <div className='title'>
                            <AssignmentTurnedInOutlinedIcon />
                            <h2>CONSULTORA</h2>
                        </div>
                        <h3>Asesoras Integrarles - Mayor retorno a su Negocio</h3>
                        <p>Servicios de asesora integral en gestin de procesos para garantizar que las soluciones tecnolgicas.</p>
                        <a className='btn_view gradient_buttons' href="#">Saber mas</a>
                    </div>
                </div>
                <div className='card_small'>
                    <div className='card_2'>
                        <div className='content_card_2'>
                            <div className='title'>
                                <ManageAccountsOutlinedIcon />
                                <h2>INNOVACIN</h2>
                            </div>
                            <h3>Reingeniera, Analtica, Gestin y Procedimientos de Datos</h3>
                            <p>Servicios de incluyen procesos de reingeniera, analtica, gestin y procedimientos de datos.</p>
                            <a className='btn_view gradient_buttons' href="#">Saber mas</a>
                        </div>
                    </div>
                    <div className='card_2'>
                        <div className='content_card_2'>
                            <div className='title'>
                                <GroupsOutlinedIcon />
                                <h2>BPO</h2>
                            </div>
                            <h3>Aplicacin de tcnicas de reingeniera - Optimizando tiempos</h3>
                            <p>Servicios tercerizados de bajo costo y alta productividad para nuestros clientes.</p>
                            <a className='btn_view gradient_buttons' href="#">Saber mas</a>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Section1